.traveller__section {
    padding-top: 48px;
        padding-bottom: 72px;
}

.traveller__sectionProf {
    display: flex;
    align-items: center;
}

.traveller__profImg {
    border-radius: 100%;
}

.travellers__sectionFlex {
    display: flex;
    flex-direction: column-reverse;

            @media screen and (min-width: 335px) {
            
        }
    
    
        @media screen and (min-width: 704px) {

        }
    
        @media screen and (min-width: 1264px) {
flex-direction: column;
        }
}

.traveller__profInfoName {
font-family: var(--font-family);
font-weight: 700;
font-size: 32px;
line-height: 130%;
letter-spacing: -0.01em;
color: var(--color-scheme-2-text);
}

.traveller__sectionTravArticles {
        margin-top: 48px;
        margin-bottom: 40px;
        font-family: var(--font-family);
font-weight: 700;
font-size: 28px;
line-height: 120%;
letter-spacing: -0.01em;
color: var(--color-scheme-1-text);

        @media screen and (min-width: 335px) {
            
        }
    
    
        @media screen and (min-width: 704px) {
font-family: var(--font-family);
font-weight: 700;
font-size: 44px;
line-height: 120%;
letter-spacing: -0.01em;
color: var(--color-scheme-1-text);
        }
    
        @media screen and (min-width: 1264px) {
font-family: var(--font-family);
font-weight: 700;
font-size: 52px;
line-height: 120%;
letter-spacing: -0.01em;
color: var(--color-scheme-1-text);
        }
}

.traveller__profInfoCount {
font-family: var(--font-family);
font-weight: 400;
font-size: 20px;
line-height: 150%;
color: var(--color-scheme-2-text);
}

.traveller__sectionList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

